body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Helvetica, sans-serif;
  font-size: calc(15px + 0.390625vw);
}

h1 {
  color: rgb(227, 220, 220);
}

p, span {
  font-size: 0.8rem;
}

.App {
  background-color: #646bea;
  height: 100vh;
  width: 100vw;
}

.container_app {
  background-color: rgba(42, 42, 98, 0.873);
  box-shadow: 1px 4px 16px 3px rgba(0,0,0,0.56);
  position: fixed;
  top: 13%;
  right: 30%;
  z-index: 1;
}

.lang_selector {
  display: flex;
  justify-content: flex-end;
  margin-right: 0.5rem;
}

.lang_selector > button {
  background-color: transparent;
  border: 1px solid rgb(84, 139, 240);
  border-radius: 10px;
  color: rgb(203, 203, 249);
  cursor: pointer;
  font-size: 0.9rem;
  padding: 0.3rem;
  outline-style: none;
}

.bi-translate {
  margin: 0 0.4rem 0 0;
}

.container_list_lang_select {
  animation: fadeInAnim 0.3s;
  background-color: rgba(255, 255, 255, 0.907);
  border-radius: 8px 3px 8px 4px;
  display: flex;
  justify-content: flex-end;
  position: absolute;
  top: 2rem;
  right: 1.5rem;
  z-index: 1;
}

.display_data {
  margin-top: 2rem;
}

.display_data > h4 {
  text-align: center;
  color: rgb(203, 203, 249);
}

/* ----> Display Dimen Components Styles <---- */
.display_dimen {
  margin-top: 1rem;
}

.display_button {
  display: flex;
  justify-content: center;
}

.btn_icon {
  background-color: transparent;
  border-radius: 13px;
  border: 1px solid rgb(84, 139, 240);;
  outline-style: none;
  padding: 3px 10px 3px 10px;
  width: 5.5rem; 
}

.display_dimensions_device {
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  height: 100%;
  height: 100%;
  z-index: 1;
}

.container_display_dimensions_device {
  background-color: white;
  border-radius: 10px;
  padding: 5px;
  height: 40%;
  width: 90%;
}

.container_display_dimensions_device > h4, p {
  text-align: center;
  padding: 5px;
  margin-bottom: 1.3rem;
}

.span_close {
  border-bottom: 1px solid rgb(230, 222, 222);
  display: flex;
  justify-content: flex-end;
  font-weight: bold;
  margin-right: 0.5rem;
  margin-bottom: 0.5rem;
  padding: 0 0 0.3rem 0;
}

.span_close > button {
  border: 1px solid rgb(84, 139, 240);
  background-color: transparent;
  border-radius: 50%;
  padding: 0.2rem 0.4rem;
  outline-style: none;
}

.info_container {
  margin-top: 3rem;
}

.info_container > p {
  font-size: 0.8rem;
  color: rgb(250, 238, 131);
}

.footer {
  position: absolute;
  bottom: 0;
  right: 0.3rem;
}

.footer > p {
  margin: 0.2rem;
  font-size: 0.8rem;
  color: rgb(237, 237, 252);
}

.circles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.circles li {
  position: absolute;
  display: block;
  list-style: none;
  width: 20px;
  height: 20px;
  background-color: rgba(255, 255, 255, 0.2);
  animation: bublesFloat 25s linear infinite;
  bottom: -150px;
  z-index: 1;
}

.circles li:nth-child(1) {
  left: 25%;
  width: 80px;
  height: 80px;
  animation-delay: 0s;
}

.circles li:nth-child(2) {
  left: 10%;
  width: 20px;
  height: 20px;
  animation-delay: 2s;
  animation-duration: 12s;
}

.circles li:nth-child(3) {
  left: 70%;
  width: 20px;
  height: 20px;
  animation-delay: 4s;
}

.circles li:nth-child(4) {
  left: 40%;
  width: 60px;
  height: 60px;
  animation-delay: 0s;
  animation-duration: 18s;
}

.circles li:nth-child(5) {
  left: 65%;
  width: 20px;
  height: 20px;
  animation-delay: 0s;
}

.circles li:nth-child(6) {
  left: 75%;
  width: 110px;
  height: 110px;
  animation-delay: 3s;
}

.circles li:nth-child(7) {
  left: 35%;
  width: 150px;
  height: 150px;
  animation-delay: 7s;
}

.circles li:nth-child(8) {
  left: 50%;
  width: 25px;
  height: 25px;
  animation-delay: 15s;
  animation-duration: 45s;
}

.circles li:nth-child(9) {
  left: 20%;
  width: 15px;
  height: 15px;
  animation-delay: 2s;
  animation-duration: 35s;
}

.circles li:nth-child(10) {
  left: 85%;
  width: 150px;
  height: 150px;
  animation-delay: 0s;
  animation-duration: 11s;
}

/* ----> ANIMATIONS <---- */
@keyframes fadeInAnim {
  from {
    opacity: 0;
  } to {
    opacity: 1;
  }
}

@keyframes bublesFloat {
  0% {
    transform: translateY(0) rotate(0deg);
    opacity: 1;
    border-radius: 0;
  }
  100% {
    transform: translateY(-1000px) rotate(720deg);
    opacity: 0;
    border-radius: 50%;
  }
}


/* ----> MEDIA QUERIES FOR MOBILE PHONE <---- */
@media only screen and (min-width: 270px) and (max-width: 450px) {
  .container_app {
    border-radius: 15px;
    height: 70%;
    width: 80%;
    position: fixed;
    right: 10%;
    padding: 0.5rem;
  }

  .lang_selector > button {
    outline-style: none;
    width: 7.2rem;
  }

  .info_container {
    margin-top: 2rem;
  }

  .container_list_lang_select {
    top: 2.7rem;
    right: 1.1rem;
    width: 7rem;
  }

  .list_lang_selector {
    padding: 0.3rem 0 0.3rem 0;
    margin-left: 0;
  }

  .list_lang_selector > button {
    cursor: pointer;
    width: 100%;
    padding: 0.2rem 0.2rem;
    font-size: 0.9rem;
    border: none;
    outline-style: none;
    background-color: transparent;
  }

  .list_lang_selector > button:hover {
    background-color: rgba(219, 219, 219, 0.877);
    color: rgb(7, 79, 107);
  }
}

/* ----> MEDIA QUERIES FOR TABLETS <---- */
@media only screen and (min-width: 451px) and (max-width: 768px) {
  .container_app {
    border-radius: 20px;
    height: 70%;
    width: 80%;
    position: fixed;
    right: 10%;
    padding: 0.5rem;
  }
}

/* ----> MEDIA QUERIES FOR SMALL SCREEN, LAPTOPS <---- */
@media only screen and (min-width: 769px) and (max-width: 1024px) {
  .container_app {
    border-radius: 30px;
    height: 70%;
    width: 75%;
    position: fixed;
    right: 12%;
    padding: 1rem;
  }
}

/* ----> MEDIA QUERIES FOR DESKTOP, LARGE SCREEN <---- */
@media only screen and (min-width: 1025px) and (max-width: 1200px) {
  .container_app {
    border-radius: 30px;
    height: 70%;
    width: 35%;
    position: fixed;
    padding: 1rem;
  }

  .container_display_dimensions_device {
    width: 60%;
  }

  .container_list_lang_select {
    position: absolute;
    top: 3.2rem;
  }

  .footer {
    position: absolute;
    bottom: 0.4rem;
  }

  .info_container {
    margin-top: 1rem;
  }
}

/* ----> MEDIA QUERIES FOR EXTRA LARGE SCREEN, TV <---- */
@media only screen and (min-width: 1201px) {
  .container_app {
    border-radius: 50px;
    height: 70%;
    width: 35%;
    position: fixed;
    padding: 1.5rem;
    right: 33%;
    top: 14%;
  }

  .container_list_lang_select {
    animation: fadeInAnim 0.3s;
    background-color: rgba(255, 255, 255, 0.907);
    border-radius: 7px 3px 8px 4px;
    position: absolute;
    top: 3.7rem;
    right: 2.2rem;
    width: 6rem;
    z-index: 1;
  }

  .lang_selector > button {
    transition: background-color 0.3s;
    width: 7.3rem;
  }

  .lang_selector > button:hover {
    background-color: rgba(171, 178, 255, 0.423);
  }

  .info_container {
    margin-top: 1rem;
  }
  
  .info_container > p {
    font-size: 0.8rem;
  }

  .btn_icon {
    cursor: pointer;
    transition: color 0.3s ease-out;
  }

  .btn_icon:hover {
    color: rgba(255, 255, 255, 0.867);
    border: 1px solid rgba(255, 255, 255, 0.85);
  }

  .footer {
    position: absolute;
    bottom: 0.3rem;
    right: 0.9rem;
  }

  .container_display_dimensions_device {
    background-color: white;
    border-radius: 10px;
    padding: 5px;
    height: 40%;
    width: 50%;
  }

  .span_close > button {
    cursor: pointer;
    font-weight: bold;
    font-size: 1rem;
    transition: color 0.1s ease-out;
  }

  .span_close > button:hover {
    background-color: rgba(255, 0, 0, 0.049);
    border: 1px solid rgba(255, 0, 0, 0.593);
    color: rgba(255, 0, 0, 0.693);
  }

  .list_lang_selector {
    padding: 0.3rem 0 0.3rem 0;
    margin-left: 0;
  }

  .list_lang_selector > button {
    cursor: pointer;
    width: 100%;
    padding: 0.2rem 0.2rem;
    font-size: 0.9rem;
    border: none;
    outline-style: none;
    background-color: transparent;
  }

  .list_lang_selector > button:hover {
    background-color: rgba(219, 219, 219, 0.877);
    color: rgb(7, 79, 107);
  }
}
